import React, { Component } from 'react';
import styles from './Resume.module.css';
import Data from './ResumeData'

class Resume extends Component {
  constructor(props){
    super(props);
    document.title = this.props.title;
  }
  renderEducation = item => {
    return (
      <div className={styles.itemContainer}>
        <div className={styles.itemTitle}>{item.name}</div>
        {
          item.data.map(item => {
            return (
              <div className={styles.itemContent}>
                <div className={styles.education} style={{ fontSize: '1.5em', fontWeight: '600' }}>{item.title}</div>
                <div className={styles.educationLocation}>{item.location}</div>
                <div className={styles.educationTime}>{item.time}</div>
              </div>
            );
          })
        }
      </div>
    );
  }
  renderExperience = exp => {
    return (
      <div className={styles.itemContainer}>
        <div className={styles.itemTitle}>{exp.name}</div>
        {
          exp.data.map(item => {
            return (
              <div className={styles.itemContent}>
                <h1 className={styles.experienceTitle}>{item.title}</h1>
                <div>
                  <a className={styles.link} href={item.url} style={{ color: '#ec407a', }}>{item.company}</a>
                  {` - ${item.time}`}
                </div>
                <p className={styles.experienceBody}>{item.description}</p>
              </div>
            );
          })
        }
      </div>
    )
  }
  renderSkills = skills => {
    return (
      <div className={styles.itemContainer}>
        <div className={styles.itemTitle}>{skills.name}</div>
        <div className={styles.skillContainer}>
        {
          skills.data.map(item => {
            return (
              <div className={styles.skillItem}>
                {
                  item.url !== '' ? <img className={styles.skillImage} src={item.url} alt={item.name} />
                  : <p>{'\u2022'}</p>
                }
                <div className={styles.skillName}>
                  <p>{item.name}</p>
                </div>
              </div>
            );
          })
        }
        </div>
      </div>
    )
  }
  renderHonors = honors => {
    return (
      <div className={styles.itemContainer}>
        <div className={styles.itemTitle}>{honors.name}</div>
        {
          honors.data.map(item => {
            return (
              <div className={styles.itemTwoColumn}>
                <div className={styles.itemLeftContent}>{item.name}</div>
                <div className={styles.itemRightContent}>{item.description}</div>
              </div>
            );
          })
        }
      </div>
    );
  }
  render() {
    return (
      <div className={styles.body}>
        <div className={styles.paperWrapper}>
          <div className={styles.topContainer}>
            <div className={styles.header}>
              <h1 className={styles.fullname}>Duc Le</h1>
              <div className={styles.rightOfFullname}>
                <a href="/Duc_Le_Resume.pdf" download="Duc_Le_Resume.pdf" ><div>Download Pdf</div></a>
                <div style={{ marginTop: '10px' }}>
                  <a href="mailto: ldcduc@apcs.vn" style={{ color: 'gray' }}>
                    <img src="../33700.png" alt="email icon" style={{ width: '1em', height: '1em' }}/>
                    {` ldcduc@apcs.vn`}
                  </a>
                  <br/>
                  <a href="tel:077-413-4201" style={{ color: 'gray' }}>
                    <img src="../9f50dce29d.svg" alt="phone icon" style={{ width: '1em', height: '1em' }}/>
                      {` (+84) 77 413 4201`}
                  </a>
                  <div style={{ color: 'gray' }}>
                    <img src="../67347.svg" alt="location icon" style={{ width: '1em', height: '1em' }}/>
                      {` Ho Chi Minh City, Vietnam`}
                  </div>
                </div>
              </div>
            </div>
            <a href="/" className={styles.link}>Portfolio</a>
            <p className={styles.briefIntro}>{Data.briefIntro}</p>
          </div>
          <div className={styles.bottomContainer}>
            <div className={styles.blockLeft}>
              {this.renderEducation(Data.education)}
              {this.renderExperience(Data.experience)}
              {this.renderHonors(Data.honors)}
            </div>
            <div className={styles.blockRight}>
              {this.renderSkills(Data.skills)}
              {this.renderSkills(Data.tool)}
              {this.renderSkills(Data.technology)}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Resume;
