import React, { Component } from 'react';
import { Link } from "react-router-dom";
import styles from '../Blog.module.css'
import stylesPage from './Page.module.css'
import DisqusComponent from '../DisqusComponent'
import { MetaTags } from 'react-meta-tags';
import ImgsViewer from 'react-images-viewer'

class BlogPage extends Component {
  imgSet = [];
  constructor(props){
    super(props);
    this.state = {
      viewerIsOpen: false,
      currentImgIndex: 0,
    }
    const { content } = this.props.data;
    content.forEach(element => {
      if (element.type === 'image')
        this.imgSet.push({
          src: element.url,
          caption: element.caption,
        });
    });
    // document.title = data.title;
  }
  handleOnClickImage = index => {
    this.setState({ viewerIsOpen: true, currentImgIndex: index, });
  }
  nextImage = () => {
    this.setState({ currentImgIndex: this.state.currentImgIndex + 1, });
  }
  previousImage = () => {
    this.setState({ currentImgIndex: this.state.currentImgIndex - 1, });
  }
  handleClickThumbnail = (index) => {
    this.setState({ currentImgIndex: index, });
  }
  closeViewer = () =>{
    this.setState({ viewerIsOpen: false, });
  }
  renderImg = img => {
    return (
    // <div className={stylesPage.image} >
    <div className={stylesPage.image} onClick={() => this.handleOnClickImage(img.index)}>
      <div>&ensp;</div>
      <img src={img.url} alt={img.alt} />
      {img.caption && <div className={stylesPage.caption}>{img.caption}</div>}
    </div>
    )
  }
  renderHeading = heading => {
    return <div className={stylesPage.heading}>{heading.body}</div>
  }
  renderParagraph = paragraph => {
    return <p style={{ textIndent: '0em' }}>{paragraph.body}</p>
  }
  renderLink = link => {
    return <a href={link.url}>{link.body}</a>
  }
  renderDate = date => {
    return <div className={stylesPage.date}>{`${date.month} ${date.day}, ${date.year}`}</div>
  }
  renderQuote = quote => {
    return <h2>{quote.body}</h2>
  }
  renderBrief = brief => {
    return <div className={stylesPage.brief}>{brief.body}</div>
  }
  render() {
    const { data, styleName, prev, next } = this.props;
    let imgIndex = 0;
    return (
      <div className={styleName}>
        <MetaTags>
          <title>{data.title}</title>
          <meta name="description" content={data.title} />
          <meta property="og:title" content={data.title} />
          <meta property="og:image" content="http://www.stickpng.com/assets/images/584ac2d03ac3a570f94a666d.png" />
        </MetaTags>
        <div className={stylesPage.title}>{data.title}</div>
        {this.renderDate(data.date)}
        <br/>
        {data.content.map(item => {
          if (item.type === 'brief')
            return this.renderBrief(item);
          else if (item.type === 'paragraph')
            return this.renderParagraph(item);
          else if (item.type === 'image')
            return this.renderImg({ ...item, index: imgIndex++ });
          else if (item.type === 'link')
            return this.renderLink(item);
          else if (item.type === 'heading')
            return this.renderHeading(item);
          else if (item.type === 'quote')
            return this.renderQuote(item); 
          else return null;
        })}

        <p>&ensp;</p>
        <div>
        {prev !== null && <Link to={`/blog/${prev.url}`} className={styles.link}>Previous article</Link>}
        {next !== null && <Link to={`/blog/${next.url}`} style={{ float: 'right', }} className={styles.link}>Next article </Link>}
        </div>
        <br/>
        <DisqusComponent url={`https://ldcduc2019.xyz${window.location.pathname}`} id={data.id}/>
        <ImgsViewer
          imgs={this.imgSet}
          currImg={this.state.currentImgIndex}
          isOpen={this.state.viewerIsOpen}
          onClickPrev={this.previousImage}
          onClickNext={this.nextImage}
          onClose={this.closeViewer}
          showThumbnails
          onClickThumbnail={this.handleClickThumbnail}
        />
      </div>
      )
    }
  }

  export default BlogPage;