          body: `Mình bắt đầu dùng máy tính cá nhân từ lúc 4 tuổi, khoảng năm 2002, tính đến bây giờ nếu làm phép trừ thì được 17 năm rồi. Theo thời gian thì mình đã dùng qua Windows 95, 98, ME, XP, 7, 8, 8.1, 10, El Capitan, Sierra, High Sierra và Mojave. Trong hơn 2 năm gần đây thì mình không có dùng máy windows nữa, cho nên mình thật sự bị sốc khi phải dùng lại máy Windows.`,
