import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import styles from './Blog.module.css'
import Blogs from './Blogs/BlogData.js'
import BlogPage from './Blogs/BlogPage'
import ScrollButton from './ScrollButton'

class Blog extends Component {
  constructor(props){
    super(props);
    this.state = {
      visibleRouter: true,
    }
    document.title = this.props.title;
  }
  render() {
    const { data } = Blogs;
    let blogData = [];
    data.forEach(page => { 
      if (page.hidden !== true)
      blogData.push(page);
    })
    return (
      <div className={styles.pageContainer} >
        <h1 className={styles.blogHeader}>My Blog</h1>
        <Router >
          <div className={styles.twoColumnContainer}>
            <div className={styles.leftColumnContainer}>
              {/* {window.location.pathname === '/blog' && */}
              {
              <>
              {`Total article: ${blogData.length}`}
              <div className={styles.blogList}>
                {blogData.map(page => {
                  if (!page.hidden) {
                    return (
                      <div className={styles.blogItem}>
                        <Link 
                          onClick={() => { this.setState({ visibleRouter: false, }) }}
                          to={`/blog/${page.url}`}
                          className={styles.link}
                        >{page.title}</Link>
                      </div>
                    )
                  }
                  else return null;
                })}
              </div>
              </>
              }
            </div>
            <div className={styles.rightColumnContainer}>
              {window.location.pathname !== '/blog' && blogData.map((page, index) => {
                return (
                  <Route 
                    exact
                    path={`/blog/${page.url}`}
                    component={() => 
                      <BlogPage 
                        styleName={styles.blogContent} 
                        data={page} 
                        next={(index+1 < blogData.length) ? blogData[index+1] : null}
                        prev={(index-1 >=0) ? data[index-1] : null}
                      />} 
                  />
                )
              })}
              <ScrollButton scrollStepInPx="50" delayInMs="10"/>
            </div>
          </div>
        </Router>
      </div>
    );
  }
}

export default Blog;